const ADD_NEWS = "news/ADD-NEWS"
const ADD_LIKE = "news/ADD-LIKE"
const CHANGE_TEXT = "news/CHANGE-TEXT";
const VISIBLE_COMMENT = "news/VISIBLE-COMMENT"


let initialState = {
    news: [
        {id: 1, text: "   ", img:"https://./%D0%BA%D0%B0%D1%80%D1%82%D0%B8%D0%BD%D0%BA%D0%B8/%D1%81%D0%BB%D0%BE%D0%B2%D0%B0%D1%80%D1%8C/%D0%BD%D0%BE%D0%B2%D0%BE%D1%81%D1%82%D1%8C.png?w=1600&h=1600",
        likesCount: 0, isLike: false,
        comments: [
            {id: 0, text: "Comment1"}
        ],
        commentVisible: false
    },
        {id: 2, text: " ", img:"https://./%D0%BA%D0%B0%D1%80%D1%82%D0%B8%D0%BD%D0%BA%D0%B8/%D1%81%D0%BB%D0%BE%D0%B2%D0%B0%D1%80%D1%8C/%D0%BD%D0%BE%D0%B2%D0%BE%D1%81%D1%82%D1%8C.png?w=1600&h=1600",
        likesCount: 0, isLike: false,
        comments: [
            {id: 0, text: "Comment1"}
        ],
        commentVisible: false
    },
        {id: 3, text: "API       ", img:"https://./%D0%BA%D0%B0%D1%80%D1%82%D0%B8%D0%BD%D0%BA%D0%B8/%D1%81%D0%BB%D0%BE%D0%B2%D0%B0%D1%80%D1%8C/%D0%BD%D0%BE%D0%B2%D0%BE%D1%81%D1%82%D1%8C.png?w=1600&h=1600",
        likesCount: 0, isLike: false,
        comments: [
            {id: 0, text: "Comment1"}
        ],
        commentVisible: false
    },
        {id: 4, text: "", img:"https://./%D0%BA%D0%B0%D1%80%D1%82%D0%B8%D0%BD%D0%BA%D0%B8/%D1%81%D0%BB%D0%BE%D0%B2%D0%B0%D1%80%D1%8C/%D0%BD%D0%BE%D0%B2%D0%BE%D1%81%D1%82%D1%8C.png?w=1600&h=1600",
        likesCount: 0,  isLike: false,
        comments: [
            {id: 0, text: "Comment1"}
        ],
        commentVisible: false
    },
    ],
    PostText: "",
    img: "data:image/png;base64,"
}

export const newsReducer = (state = initialState, action) => {
    switch(action.type) {
        case ADD_NEWS: {
            let newPost = {
                id: state.news.length + 1,
                text: state.PostText,
                img: state.img,
                likesCount: 0,
                comments: [],
                commentVisible: false
            };
            return {
                ...state,
                news: [newPost, ...state.news],
                PostText: ''
            };// redux  ,      state   
        }
        case ADD_LIKE: {
            return {
                ...state,
                news: state.news.map(news => {
                    if(news.id === action.id) {
                        if(!news.isLike)
                        return {...news, likesCount: news.likesCount + 1, isLike: true}
                        else return {...news, likesCount: news.likesCount - 1, isLike: false}
                    }
                    return news
                })
            };// redux  ,      state   
        }
        case VISIBLE_COMMENT: {
            return {
                ...state,
                news: state.news.map(news => {
                    if(news.id === action.id) {
                        if(!news.commentVisible)
                        return {...news, commentVisible: true}
                        else return {...news, commentVisible: false}
                    }
                    return news
                })
            };// redux  ,      state   
        }
        case CHANGE_TEXT: {
            return {
                ...state,
                PostText: action.text
            }
        }
        default:
            return state;
    }
}


export const AddNews = () => ({
    type: ADD_NEWS,
})

export const TextChange = (text) => ({
    type: CHANGE_TEXT,
    text
})

export const AddLike = (id) => ({
    type: ADD_LIKE,
    id
})

export const setVisibleComment = (id) => ({
    type: VISIBLE_COMMENT,
    id
})
